.table-responsive {
	margin: 30px 0
}

.table-wrapper {
	background: #fff;
	padding: 20px 25px;
	border-radius: 3px;
	min-width: 1000px;
	box-shadow: 0 1px 1px rgba(0, 0, 0, .05)
}

.table-title {
	padding-bottom: 15px;
	background: #3f3f3f;
	color: #fff;
	padding: 16px 30px;
	min-width: 100%;
	margin: -20px -25px 10px;
	border-radius: 3px 3px 0 0
}

.table-title h2 {
	margin: 5px 0 0;
	font-size: 24px
}

.table-title .btn {
	color: #fff;
	float: right;
	font-size: 13px;
	border: none;
	min-width: 50px;
	border-radius: 2px;
	border: none;
	outline: 0 !important;
	margin-left: 10px
}

.table-title .btn i {
	float: left;
	font-size: 21px;
	margin-right: 5px
}

.table-title .btn span {
	float: left;
	margin-top: 2px
}

table.table tr td,
table.table tr th {
	border-color: #e9e9e9;
	padding: 12px 15px;
	vertical-align: middle
}

table.table tr th:first-child {
	width: 100px
}

table.table tr th:last-child {
	width: 100px
}

table.table-striped tbody tr:nth-of-type(odd) {
	background-color: #fcfcfc
}

table.table-striped.table-hover tbody tr:hover {
	background: #f5f5f5
}

table.table th i {
	font-size: 13px;
	margin: 0 5px;
	cursor: pointer
}

table.table td:last-child i {
	opacity: .9;
	font-size: 22px;
	margin: 0 5px
}

table.table td a {
	font-weight: 700;
	color: #566787;
	display: inline-block;
	text-decoration: none;
	outline: 0 !important
}

table.table td a:hover {
	color: #2196f3
}

table.table td a.edit {
	color: #ffc107
}

table.table td a.delete {
	color: #f44336
}

table.table td i {
	font-size: 19px
}

.hint-text {
	float: left;
	margin-top: 10px;
	font-size: 13px
}

.modal .modal-body {
	padding: 20px 30px
}

.modal .modal-title {
	display: inline-block
}

.modal .btn {
	border-radius: 2px;
	min-width: 100px
}

.actions {
	display: flex;
	align-items: center;
	justify-content: center;
	border: 3px solid #000;
	margin-top: 35px
}

td {
	border: 2px solid red;
	text-align: center
}

th {
	text-align: center
}

body {
	background: #f4f4f4
}